@import '../../styles/variables.scss';


@mixin centre() {
    display: flex;
    align-items: center;
    justify-content: center;
}
.container {
    @include centre();
    background-color: $bg;
    height: 100vh;
    width: 100%;
}

.entry {
    background-color: $neutral;
    @include centre();
    padding: 2rem;
    flex-direction: column;
    width: 400px;
    border-radius: 20px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;

    &__head {
        text-align: center;
        display: flex;
        flex-direction: column;
        gap: .5rem;
        width: 360px;

        p {
            padding: 0 2.5rem 2rem;
        }
    }

    &__form {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: .5rem;

        a {
            text-decoration: none;
            padding: 0.3rem 0 1rem;
        }

        .pass {
            color: red;
            font-size: 14px;
        }

        .btn {
            margin: 1rem 0;
        }

        button {
            border: none;
            width: 100%;
            background-color: $secondary;
            height: 40px;
            font: inherit;
            border-radius: $border-radius;
            color: $neutral;
            cursor: pointer;
        }
    }

    &__group {
        width: 100%;
        border: 1px solid $grey;
        display: flex;
        align-items: center;
        height: 40px;
        border-radius: $border-radius;
        padding: 0 1rem;
        justify-content: space-between;

        .icon{
            @include centre();
            cursor: pointer;
        }

        .check {
            color: green;
        }

        .close {
            color: red;
        }

        .verify__icon {
            @include centre();
            display: none;

        }

        .verify__icon.active {
            display: flex;
        }

        svg {
            width: 20px;
            height: 20px;
        }

        input {
            outline: none;
            border: none;
            height: 100%;
            background: none;
            font: inherit;
        }

        input::placeholder {
            color: $grey;

        }

        

    }

    &__group.error {
        border: 1px solid red;
    }


    &__socials{
        @include centre();
        flex-direction: column;
        gap: 1rem;
        padding: 1rem 0;

        a {
            color: $primary;
            font-weight: 700
        }
    }

    .social__group{
        @include centre();
        gap: .5rem;
        width: 100%
    }

    .social {
        @include centre();
        gap: .5rem;
        padding: 0 1rem;
        border: 1px solid $grey;
        height: 35px;
        border-radius: $border-radius;
        cursor: pointer;

        svg {
            width: 20px;
            height: 20px;

        }
    }
}